<template>
    <div class="view">
        <div class="rl_main">
            <div class="rl_h1">文档名称：{{newFile.title}}</div>
            <!-- <div class="rl_element">文档要素：
                <el-button :key="index" type="primary" size="mini" round :plain="!item.sign" 
                v-for="(item, index) in newFile.element" @click="screenContent(item)">{{item.value}}</el-button>
            </div> -->
            <div class="title_tag">审判流程</div>
            <div v-for="(item, index) in newFile.relaInfo_docCont" :key="index">
                <div class="document_tag"><span>{{item.name}}</span>：{{item.value[0]}}</div>
            </div>
            <div class="clear"></div>

            <div class="title_tag">人员信息</div>
            <div>
                <div class="detail_rbase">
                    <p class="detail_rbasel2">原告</p> 
                    <div class="detail_rbaser2">
                        <ul v-for="(item, index) in personnelInfo.plaintiff" :key="index">
                            <div><el-tag>{{item.role}}</el-tag></div>
                            <li v-for="(item2, index2) in item.attribute" :key="index2"><el-tag type="info">{{item2}}</el-tag></li>
                        </ul>
                    </div>
                    <p class="detail_rbasel2">被告</p> 
                    <div class="detail_rbaser2">
                        <ul v-for="(item, index) in personnelInfo.defendant" :key="index">
                            <div><el-tag>{{item.role}}</el-tag></div>
                            <li v-for="(item2, index2) in item.attribute" :key="index2"><el-tag type="info">{{item2}}</el-tag></li>
                        </ul>
                    </div>
                    <p class="detail_rbasel2">委托代理人</p> 
                    <div class="detail_rbaser2">
                        <ul v-for="(item, index) in personnelInfo.agent" :key="index">
                            <div><el-tag>{{item.role}}</el-tag></div>
                            <li v-for="(item2, index2) in item.attribute" :key="index2"><el-tag type="info">{{item2}}</el-tag></li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="clear"></div>

            <div class="title_tag">案由专属</div>
            <div class="document_tag" v-if="causeList.mortgage.toString()"><span>抵押</span>：{{causeList.mortgage.goods.toString()}}</div>
            <div class="document_tag" v-if="causeList.pledge.toString()"><span>质押</span>：{{causeList.pledge.toString()}}</div>
            <div class="document_tag" v-if="causeList.delay_pay.toString()"><span>滞纳金</span>：{{causeList.delay_pay.toString()}}</div>
            <div class="document_tag" v-if="causeList.daily_rate.toString()"><span>日利率</span>：{{causeList.daily_rate.toString()}}</div>
            <div class="document_tag" v-if="causeList.monthly_rate.rate"><span>月利率</span>：{{causeList.monthly_rate.rate}}</div>
            <div class="document_tag" v-if="causeList.annual_rate.toString()"><span>年利率</span>：{{causeList.annual_rate.toString()}}</div>
            <div class="document_tag" v-if="causeList.appeal_amount.amount"><span>诉请金额</span>：{{causeList.appeal_amount.amount}}</div>
            <div class="document_tag" v-if="causeList.affirm_amount.amount"><span>认定金额</span>：{{causeList.affirm_amount.amount}}</div>
            <div class="document_tag" v-if="causeList.judge_amount.amount"><span>判决金额</span>：{{causeList.judge_amount.amount}}</div>
            <div class="clear"></div>
        </div>
        <div class="main">
            <el-tabs type="border-card" v-model="editableTabsValue">
                <el-tab-pane label="文书内容" name="1">
                    <div ref="document" class="document" v-html="newFile.content">{{newFile.content}}</div>
                </el-tab-pane>
                <el-tab-pane label="类案推送" name="2">
                    <ul class="al_ul">
                        <li v-for="(item, index) in tableData" :key="index" @click="details(item.docId)">
                            <div class="alul480" style="width: 900px;">
                                <div class="alul480top">
                                    <p class="alul480p">{{item.title}}</p>
                                </div>
                                <div class="alul480center">
                                    <p>文档ID：{{item.docId}}</p>
                                </div>
                                <!-- 文本要素 -->
                                <div class="clear"></div>
                                <div class="alul480bottom" style="margin-left:10px">
                                    <span>案由：{{item.rela.案由}}</span> 
                                    <span>法院：{{item.rela.审理法院}}</span> 
                                    <span>判决日期：{{item.rela.裁判日期}}</span> 
                                <!-- <div class="al_ul_elements">
                                    <el-tag type="warning" size="mini" v-for="(item, index) in item.element" :key="index">{{item}}</el-tag>
                                </div> -->
                                </div>
                            </div>
                            <div class="alulright">
                                <div class="right_rate">
                                    <div style="color: rgb(247, 186, 42); float:right; line-height: 20px">{{item.score}}</div>
                                    <el-rate
                                        style="float:right"
                                        v-model="item.scores"
                                        disabled
                                        text-color="#ff9900">
                                    </el-rate>
                                </div>
                            </div>
                        </li>
                    </ul>
                </el-tab-pane>
            </el-tabs>
        </div>
        <main-footer></main-footer>
    </div>
</template>

<script>
import mainHeader from '@/components/common/header'
import mainFooter from '@/components/common/footer'
import { new_docs_detail } from '@/api/import.js'
import { model_detail } from '@/api/model.js'

export default {
    data(){
        return{
            editableTabsValue: '2',
            tableData: [],
            newFile: {
                id: '',
                title: '',
                content: '',
                element: [],
                relaInfo_docCont: []
            },
            personnelInfo: {
                plaintiff: [],
                defendant: [],
                agent: []
            },
            causeList: {
                mortgage: '', //抵押
                pledge: '', //质押
                delay_pay: '', //滞纳金
                daily_rate: '', //日利率
                monthly_rate: '', //月利率
                annual_rate: '', //年利率
                appeal_amount: '', //诉请金额
                affirm_amount: '', //认定金额
                judge_amount: '' //判决金额
            }
        }
    },
    mounted(){
        let docId = this.$route.query.docId;
        this.getDetail(docId);
    },
    methods:{
        getDetail(id){
            new_docs_detail(id).then(res => {
                this.causeList = Object.assign(this.causeList, res.data);
                
                for(let item of res.data.roleInfo){
                    if(item.key == 'plaintiff'){
                        this.personnelInfo.plaintiff.push(item);
                    }
                    if(item.key == 'defendant'){
                        this.personnelInfo.defendant.push(item);
                    }
                    if(item.key == 'agent'){
                        this.personnelInfo.agent.push(item);
                    }
                }

                this.newFile.id = res.data.docId;
                this.newFile.title = res.data.doc_name;
                this.newFile.content = res.data.docContent.replace(/\\n/g, '</br></br>');
                this.newFile.relaInfo_docCont = res.data.relaInfo_docCont;
                
                this.newFile.element = res.data.element.map(item => {
                    return {value: item, sign: false, handle: 0}
                })
               
                this.tableData = res.data.recommend.map(item => { 
                    item.scores = parseFloat(((item.score - 0.1) / 2).toFixed(1));
                    return item
                });

                console.log(this.tableData);
            })
        },
        details(id){
            this.$router.push({
                path: '/upload/detail',
                query: {
                    docId: id
                }
            }); 
        },
        screenContent(obj){
            obj.sign = !Boolean(obj.sign);
            if(obj.sign){
                this.newFile.content = this.newFile.content.replace(new RegExp(obj.value,'g'), `<span class="to_tag">${obj.value}</span>`);
            }else{
                this.newFile.content = this.newFile.content.replace(new RegExp(`<span class="to_tag">${obj.value}</span>`,'g'), obj.value);
            }

            this.editableTabsValue = '1';
            this.$forceUpdate();
        }
    },
    components: {
        mainHeader,
        mainFooter
    }
}
</script>

<style scoped>
.main{
    padding-bottom: 0;
}
.al_ul li:last-child{
    border-bottom: 0;
}
.rl_main{
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
}
.alulright{
    width: 140px;
}
</style>


