import { upload_file, recommend } from '@/api/import.js'

const reg = /\.(txt?)$/i;

var uploadFile = {
	beforeRemoveF(file, fileList) {
		if(reg.test(file.name)){
			return this.$confirm(`确定移除 ${ file.name }？`);
		}
	},
	beforeUploadF(file) {
	    if (!reg.test(file.name)) {
			this.$notify.closeAll();
			this.$msg.warning('只能上传txt格式文件！');
	      	return false;
		}
	},
	dealNewFile({file}) {
		this.uploading = true;
		this.formdata.append('text', file);

		clearTimeout(this.timeout);
		this.timeout = setTimeout(() => {
			//发送上传请求
			upload_file(this.formdata).then((res) => {
				this.fileStatus = Object.assign(this.fileStatus, res.stat);
				this.fileStatus.data = res.data;
				this.dialogFileList = true;

				//结果推送
				this.toRecommend(res.data);

				this.$notify.closeAll();
				this.$msg.success('上传成功！');
				this.dialogFile = false;
				this.uploading = false;
				
				this.fileList = [];
				this.formdata = new FormData();
			}).catch((err) => {
				this.uploading = false;
				console.log(err);
			});
		}, 200)
	},
	toRecommend(data){
		for(let item of data){
			recommend({docId: item.docId}).then(res => {
				this.getPage();
			})
		}
	},
	submitFile(){
		this.formdata = new FormData();
		this.fileStatus = {
			data: [],
			all: 0,
			err: 0,
			ok: 0
		}
		this.$refs.uploadFile.submit();
	}
}
export {uploadFile}


