<template>
    <div class="header">
        <div class="header_main">
            <div class="logo">
                <img src="@/images/logo.png" alt="" width="100%">
				<p class="logo_title">类案推送系统</p>
            </div>
            <div class="nav">
                <div @click="linkTo(1)">
                    <a href="javascript:;" :class="count == 1 ? 'nav_a_choose' : ''">案例导入</a>
                    <div :class="count == 1 ? 'nav_hover_choose' : 'nav_hover'"></div>
                </div>
                <div @click="linkTo(2)">
                    <a href="javascript:;" :class="count == 2 ? 'nav_a_choose' : ''">文档库</a>
                    <div :class="count == 2 ? 'nav_hover_choose' : 'nav_hover'"></div>
                </div>
            </div>
            <div class="header_right">
                <div class="header_user">
                    <div class="header_icons">
						<img src="@/images/head.png" width="100%">
					</div>
                    <p class="header_admin">{{loginName}}</p>
                </div>
            </div>
        </div>
    </div> 
</template>
<script>

export default {
    data(){
        return {
			loginName: 'admin',
			count: 1
        }
	},
	created(){
		var dealPath = this.$route.path.split('/').slice(0, 2).join('/');
		switch(dealPath){
			case '/upload':
				this.count = 1;
				break;
			case '/list':
				this.count = 2;
				break;
			default: break;
		}
	},
	watch: {
		$route: function(value){
			var dealPath = value.path.split('/').slice(0, 2).join('/');
			switch(dealPath){
				case '/upload':
					this.count = 1;
					break;
				case '/list':
					this.count = 2;
					break;
				default: break;
			}
		}
	},
	methods: {
		linkTo(count){
			switch(count){
				case 1:
					this.$router.push('/upload');
					break;
				case 2:
					this.$router.push('/list');
					break;
				default: break;
			}
		},
		loginOut(){
			this.$confirm('即将退出该账号, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				sessionStorage.removeItem('token');
				sessionStorage.removeItem('userName');
				sessionStorage.clear();

				this.$msg.success(this, '您已成功退出');
				this.routerRefresh();
			})
		}
	}
}
</script>

<style scoped>
	.header{
		width: 100%;
		height: 72px;
		background: #fff;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		box-shadow: 0 0 20px rgba(0, 0, 0, .5);
	}
	.header_main{
		width: 1168px;
		height: 51px;
		margin: 0 auto;
		padding-top: 21px;
	}
	.logo{
		float: left;
		margin-top: -4px;
	}
	.logo img{
		width: 36px;
		float: left;
	}
	.logo_title{
		font-size: 24px;
		float: left;
		color: #ff730b;
		font-weight: bold;
		letter-spacing: 2px;
		margin-left: 7px;
	}
	.nav{
		height: 51px;
		line-height: 30px;
		margin-left: 280px;
		float: left;
	}
	.nav>div{
		height: 51px;
		float: left;
		position: relative;
		overflow: hidden;
	}
	.nav>div:hover .nav_hover{
		bottom: -6px;
	}
	.nav>div:hover a{
		color: #ff730b;
	}
	.nav>div a{
		padding: 0 30px;
		padding-bottom: 20px;
		color: #666666;
		font-size: 16px;
	}
	.nav_hover{
		position: absolute;
		bottom: -10px;
		left: 0;
		width: 100%;
		height: 10px;
		border-radius: 3px;
		background: #ff730b;
		transition: 0.1s;
	}
	.nav_hover_choose{
		position: absolute;
		bottom: -6px;
		left: 0;
		width: 100%;
		height: 10px;
		border-radius: 3px;
		background: #ff730b;
		transition: 0.1s;
	}
	.nav_a_choose{
		color: #ff730b !important;
	}
    .header_right {
		float: right;
		height: 35px;
		margin-right: 28px;
	}
	.header_user{
		float:left;
		cursor: pointer;
		position: relative;
		min-width: 70px;
		height: 35px;
	}
	.header_user:hover .header_block {
		display: block;
	}
	.header_icons {
		float: left;
		width: 36px;
		height: 36px;
		margin-top: -4px;
		overflow: hidden;
		border-radius: 50%;
	}
	.header_admin {
		float: left;
		line-height: 28px;
		font-size: 16px;
		margin-left: 14px;
		color: #333;
	}
	.header_block {
		width: 114px;
		box-shadow: 0 0 7px rgba(0,0,0,.2);
		position: absolute;
		top: 35px;
		right: -26px;
		z-index: 60;
		background: #fff;
		display: none;
		cursor: pointer;
	}
	.header_block_icon {
		width: 18px;
		float: left;
		margin-top: 10px;
		margin-left: 15px;
	}
	.header_block_txt {
		float: left;
		margin-left: 10px;
		line-height: 40px;
		height: 35px;
		font-size: 12px;
		color: #7c8196;
	}
	.header_block_admin {
		width: 100%;
		height: 38px;
		border-bottom: 1px solid #bbe9ff;
	}

	.header_block_exit {
		width: 100%;
		height: 38px;
	}
	.header_lean{
		width: 1px;
		height: 16px;
		margin-right: 30px;
		margin-top: 5px;
		float: left;
		border-right: 1px solid #fff;
	}
	.header_iconld{
		width: 20px;
		height: 20px;
		float: left;
		margin-right: 10px;
		margin-top: 2px;
	}
	.header_sur{
		float: left;
		font-size: 16px;
		line-height: 26px;
		color: #fff;
		margin-right: 30px;
	}
	.header_sur span{
		font-size: 16px;
		color: #fffc00;
	}
</style>





