<template>
    <div id="app" v-cloak>
      <main-header></main-header>
      <transition name="breadcrumb" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
</template>

<script>
import mainHeader from '@/components/common/header'

export default {
	components: {
		mainHeader,
	}
};
</script>

<style scoped>
  #app{
    width: 100%;
    height: 100%
  }
  #appweb{
    width: 100%;
    height: 100%
  }
  .menu_content{
    width: 100%;
    height: calc(100% - 50px);
  }
  .right_content{
    width: calc(100% - 171px);
    height: 100%; 
    float: right;
    overflow: auto;
    position: relative;
  }
  .content{
    width: calc(100% - 40px);
    min-height: calc(100% - 40px);
    background: #fff;
    margin: 20px;
    overflow-x: hidden;
  }
</style>



