import Vue from 'vue'
import Router from 'vue-router'

import upload from '@/components/upload'
import recommend from '@/components/recommend'

import list from '@/components/list'
import listDetail from '@/components/list_detail'

Vue.use(Router)

export default new Router({
  routes: [
        {
            path: '/',
            redirect: '/upload'
        },{
            path: '/upload',
            component: upload,
            meta: { filter: true }
        },{
            path: '/upload/recommend',
            component: recommend,
            meta: { filter: true }
        },{
            path: '/upload/detail',
            component: listDetail,
            meta: { filter: true }
        },{
            path: '/list',
            component: list,
            meta: { filter: true }
        },{
            path: '/list/detail',
            component: listDetail,
            meta: { filter: true }
        }
    ]
})



