<template>
    <div class="view">
        <!-- <main-header></main-header> -->
        <div class="main">
            <ul class="al_ul" v-loading="listLoading" element-loading-text="loading...">
                <li v-for="(item, index) in tableData" :key="index" @click="details(item.docId)">
                    <div class="alul480" style="width: 950px;">
                        <div class="alul480top">
                            <p class="alul480p">{{item.doc_name}}</p>
                        </div>
                        <div class="alul480center">
                            <p>文档ID：{{item.docId}}</p>
                        </div>
                        <div class="alul480bottom">
                            <span>案由：{{item.relaInfo_reason}}</span> 
                            <span>法院：{{item.relaInfo_court}}</span> 
                            <span>判决日期：{{item.relaInfo_trialDate}}</span> 
                        </div>
                    </div>
                    <div class="alulright">
                        <div class="alulrighticon" style="width: 32px"><img src="@/images/word.png" alt="" width="100%"></div>
                        <div class="alultag">
                            <img src="@/images/tag1.png" alt="" width="100%">
                            <div class="alultag_txt">{{item.relaInfo_docType}}</div>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="page_size">
                <div class="page_block">
                   <el-pagination
                        background
                        layout="total, prev, pager, next, jumper"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        :total="pageCount">
                    </el-pagination>
                </div>
            </div>
            <div class="clear"></div>
        </div>
        <main-footer></main-footer>
    </div>
</template>

<script>
import mainHeader from '@/components/common/header'
import mainFooter from '@/components/common/footer'
import { pageOptions } from '@/utils/options.js'
import { model_list, model_detail } from '@/api/model.js'

export default {
    data(){
        return{
            listLoading: false,
            tableData: [],
            ...pageOptions
        }
    },
    mounted(){
        this.getPage();
    },
    methods:{
        getPage(){
            this.listLoading = true;
            model_list({
                page: this.currentPage,
                per_page: this.pageSize
            }).then(async res => {
                this.tableData = [];
                this.pageCount = res.data.total;
                this.tableData = res.data.docsList;
                await this.getElements(this.tableData);

                this.listLoading = false;
                this.$forceUpdate();
            })
        },
        async getElements(list){
            for(let i=0; i<list.length; i++){
                await model_detail(list[i].docId).then(res => {
                    this.tableData[i].relaInfo_reason = res.data.relaInfo_reason;
                    this.tableData[i].relaInfo_court = res.data.relaInfo_court;
                    this.tableData[i].relaInfo_trialDate = res.data.relaInfo_trialDate;
                    this.tableData[i].relaInfo_docType = res.data.relaInfo_docType;
                })
            }
        },
        details(id){
            this.$router.push({
                path: '/list/detail',
                query: {
                    docId: id
                }
            }); 
        },
        handleCurrentChange(count){
            window.scrollTo(0,0);
            this.currentPage = count;
            this.getPage();
        }
    },
    components: {
        mainHeader,
        mainFooter
    }
}
</script>

<style scoped>
    .alul480bottom{
        margin-left: 10px;
    }
</style>


