<template>
    <div class="view">
        <!-- <main-header></main-header> -->
        <div class="main" v-loading="listLoading" element-loading-text="loading...">
            <div class="detail_left">
                <!-- 人员信息 -->
                <div class="detail_ltitle" @click="showThree = !showThree">
                    <div class="detail_licon"><img src="@/images/ry.png" alt="" width="100%"></div> 
                    <div class="detail_lh">人员信息</div>
                </div>
                <transition name="el-zoom-in-top">
                    <el-scrollbar class="detail_statute" style="height: 450px" v-show="showThree">
                        <div class="detail_rbase">
                            <p class="detail_rbasel2">原告</p>
                            <div class="detail_rbaser2">
                                <ul v-for="(item, index) in personnelInfo.plaintiff" :key="index">
                                    <div v-if="item.role"><el-tag>{{item.role}}</el-tag></div>
                                    <li v-for="(item2, index2) in item.attribute" :key="index2"><el-tag type="info" v-if="item2">{{item2}}</el-tag></li>
                                </ul>
                            </div>
                            <p class="detail_rbasel2">被告</p>
                            <div class="detail_rbaser2">
                                <ul v-for="(item, index) in personnelInfo.defendant" :key="index">
                                    <div v-if="item.role"><el-tag>{{item.role}}</el-tag></div>
                                    <li v-for="(item2, index2) in item.attribute" :key="index2"><el-tag type="info" v-if="item2">{{item2}}</el-tag></li>
                                </ul>
                            </div>
                            <p class="detail_rbasel2">委托代理人</p>
                            <div class="detail_rbaser2">
                                <ul v-for="(item, index) in personnelInfo.agent" :key="index">
                                    <div v-if="item.role"><el-tag>{{item.role}}</el-tag></div>
                                    <li v-for="(item2, index2) in item.attribute" :key="index2"><el-tag type="info" v-if="item2">{{item2}}</el-tag></li>
                                </ul>
                            </div>
                        </div>
                    </el-scrollbar>
                </transition>

                <!-- 文书内容 -->
                <div class="detail_ltitle" style="margin-top: 50px" @click="showTwo = !showTwo">
                    <div class="detail_licon"><img src="@/images/ws.png" alt="" width="100%"></div> 
                    <div class="detail_lh">文书内容</div>
                </div>
                <transition name="el-zoom-in-top">
                    <el-scrollbar class="detail_statute" v-show="showTwo">
                        <div class="detail_h1">{{title}}</div>
                        <div class="detail_h3" v-html="content">{{content}}</div>
                    </el-scrollbar>
                </transition>

                <!-- 引用法规 -->
                <div class="detail_ltitle" style="margin-top: 50px" @click="showOne = !showOne">
                    <div class="detail_licon"><img src="@/images/fg.png" alt="" width="100%"></div> 
                    <div class="detail_lh">引用法规</div>
                </div>
                <transition name="el-zoom-in-top">
                    <el-scrollbar class="detail_statute" style="height: 420px" v-show="showOne">
                        <div v-for="(item, index) in lawsList" :key="index">
                            <div class="detail_h1">{{item.legalRuleName}}</div>
                            <div v-for="(jtem, index) in item.Items" :key="index">
                                <div class="detail_h2">【{{jtem.legalName}}】</div>
                                <div class="detail_h3">{{jtem.legalContent}}</div>
                            </div>
                        </div>
                    </el-scrollbar>
                </transition>
            </div>
            <div class="detail_right">
                <!-- 审判流程 -->
                <div class="detail_rtitle">
                    <div class="detail_ricon"><img src="@/images/pel.png" alt="" width="100%"></div> 
                    <div class="detail_rh">审判流程</div>
                </div>
                <div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">审理法院</p> <p class="detail_rbaser">{{relaInfo.court}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">案件类型</p> <p class="detail_rbaser">{{relaInfo.caseType}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">案由</p> <p class="detail_rbaser">{{relaInfo.reason}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">审理程序</p> <p class="detail_rbaser">{{relaInfo.trialRound}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">裁判日期</p> <p class="detail_rbaser">{{relaInfo.trialDate}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">当事人</p> <p class="detail_rbaser">{{relaInfo.appellor}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">文书类型</p> <p class="detail_rbaser">{{relaInfo.docType}}</p>
                    </div>
                    <div class="detail_rbase">
                        <p class="detail_rbasel">程序类型</p> <p class="detail_rbaser">{{relaInfo.processType}}</p>
                    </div>
                </div>
                <!-- 案由专属 -->
                <div class="detail_rtitle">
                    <div class="detail_ricon"><img src="@/images/pel.png" alt="" width="100%"></div> 
                    <div class="detail_rh">案由专属</div>
                </div>
                <div>
                    <div class="detail_rbase" v-if="causeList.mortgage.toString()">
                        <p class="detail_rbasel">抵押</p> <p class="detail_rbaser">{{causeList.mortgage.goods.toString()}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.pledge.toString()">
                        <p class="detail_rbasel">质押</p> <p class="detail_rbaser">{{causeList.pledge.toString()}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.delay_pay.toString()">
                        <p class="detail_rbasel">滞纳金</p> <p class="detail_rbaser">{{causeList.delay_pay.toString()}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.daily_rate.toString()">
                        <p class="detail_rbasel">日利率</p> <p class="detail_rbaser">{{causeList.daily_rate.toString()}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.monthly_rate.rate">
                        <p class="detail_rbasel">月利率</p> <p class="detail_rbaser">{{causeList.monthly_rate.rate}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.annual_rate.toString()">
                        <p class="detail_rbasel">年利率</p> <p class="detail_rbaser">{{causeList.annual_rate.toString()}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.appeal_amount.amount">
                        <p class="detail_rbasel">诉请金额</p> <p class="detail_rbaser">{{causeList.appeal_amount.amount}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.affirm_amount.amount">
                        <p class="detail_rbasel">认定金额</p> <p class="detail_rbaser">{{causeList.affirm_amount.amount}}</p>
                    </div>
                    <div class="detail_rbase" v-if="causeList.judge_amount.amount">
                        <p class="detail_rbasel">判决金额</p> <p class="detail_rbaser">{{causeList.judge_amount.amount}}</p>
                    </div>
                </div>
            </div>
        </div>
        <main-footer></main-footer>
    </div>
</template>

<script>
import mainHeader from '@/components/common/header'
import mainFooter from '@/components/common/footer'
import { model_detail } from '@/api/model.js'

export default {
    data(){
        return{
            lawsList: [],
            baseList: [],
            relaInfo: {},
            personnelInfo: {
                plaintiff: [],
                defendant: [],
                agent: []
            },
            causeList: {
                mortgage: '', //抵押
                pledge: '', //质押
                delay_pay: '', //滞纳金
                daily_rate: '', //日利率
                monthly_rate: '', //月利率
                annual_rate: '', //年利率
                appeal_amount: '', //诉请金额
                affirm_amount: '', //认定金额
                judge_amount: '' //判决金额
            },
            title: '',
            content: '',
            showOne: true,
            showTwo: true,
            showThree: true,
            listLoading: false
        }
    },
    mounted(){
        window.scrollTo(0,0);
        let docId = this.$route.query.docId;
        this.getDetail(docId);
    },
    methods:{
        getDetail(id){
            this.listLoading = true;
            model_detail(id).then(res => {
                this.causeList = Object.assign(this.causeList, res.data);

                for(let item of res.data.roleInfo){
                    if(item.key == 'plaintiff'){
                        this.personnelInfo.plaintiff.push(item);
                    }
                    if(item.key == 'defendant'){
                        this.personnelInfo.defendant.push(item);
                    }
                    if(item.key == 'agent'){
                        this.personnelInfo.agent.push(item);
                    }
                }

                this.title = res.data.doc_name;
                this.content = res.data.docContent.replace(/\\n/g, '</br></br>');
                this.lawsList = res.data.legalBase;
                this.baseList = res.data.relaInfo;

                this.relaInfo.appellor = res.data.relaInfo_appellor;
                this.relaInfo.caseType = res.data.relaInfo_caseType;
                this.relaInfo.court = res.data.relaInfo_court;
                this.relaInfo.docType = res.data.relaInfo_docType;
                this.relaInfo.processType = res.data.relaInfo_processType;
                this.relaInfo.reason = res.data.relaInfo_reason;
                this.relaInfo.trialDate = res.data.relaInfo_trialDate;
                this.relaInfo.trialRound = res.data.relaInfo_trialRound;

                this.listLoading = false;
            })
        }
    },
    components: {
        mainHeader,
        mainFooter
    }
}
</script>

<style scoped>
.view{
    background: #fff;
}
</style>


