<template>
    <div class="view">
        <!-- <main-header></main-header> -->
        <div class="main">
            <el-button class="btn" type="primary" plain size="small" icon="el-icon-upload2" @click="dialogFile = true">上传文件</el-button>
            <div class="clear"></div>
            <ul class="al_ul" v-loading="listLoading" element-loading-text="loading...">
                <li v-for="(item, index) in tableData" :key="index" @click="details(item.docId, item.recommend_status)">
                    <div class="alul480" style="width: 600px;">
                        <div class="alul480top">
                            <p class="alul480p">{{item.doc_name}}</p>
                        </div>
                        <div class="alul480center" style="height: 30px; margin-bottom: 20px">
                            <!-- 图标 -->
                            <div class="al_acends" v-if="item.newdoc_status == '要素提取成功'">
                                <div class="al_acends_txt al_acings_txt">{{item.newdoc_status}}</div>
                            </div>
                            <div class="al_acends" v-else>
                                <div class="al_acends_txt">{{item.newdoc_status}}</div>
                            </div>

                            <div class="al_action" v-if="item.recommend_status == '类案推荐成功'">
                                <div class="al_acends_txt al_acings_txt">{{item.recommend_status}}</div>
                            </div>
                            <div class="al_action" v-else>
                                <div class="al_acends_txt">{{item.recommend_status}}</div>
                            </div>
                        </div>
                        <div class="alul480center">
                            <p>文档ID：{{item.docId}}</p>
                        </div>
                        <div class="alul480bottom">
                            <span>案由：{{item.relaInfo_reason}}</span> 
                            <span>法院：{{item.relaInfo_court}}</span> 
                            <span>判决日期：{{item.relaInfo_trialDate}}</span> 
                        </div>
                        <!-- <div class="alul480bottom">{{item.docContent}}</div> -->
                    </div>
                    <!-- 图标 -->
                    <div class="alulright">
                        <div class="alulrighticon" style="width: 32px; margin-top:15px"><img src="@/images/word.png" alt="" width="100%"></div>
                        <p data-v-7db7f854="" class="alulrightdate">{{item.create_at}}</p>
                    </div>
                    <!-- 文本要素 -->
                    <div class="clear"></div>
                    <div class="al_ul_elements" v-if="item.newdoc_status == '要素提取成功'">
                        <el-tag size="mini" v-for="(item, index) in item.element" :key="index">{{item}}</el-tag>
                    </div>
                </li>
            </ul>
            <div class="page_size">
                <div class="page_block">
                   <el-pagination
                        background
                        layout="total, prev, pager, next, jumper"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        :total="pageCount">
                    </el-pagination>
                </div>
            </div>
            <div class="clear"></div>
        </div>

        <el-dialog title="知识导入" :close-on-click-modal="false" :visible.sync="dialogFile" width="550px">
            <el-form ref="dataForm" label-position="left" label-width="84px">
                <el-form-item label="上传文件" required>
                <el-upload class="upload-demo" 
                    ref="uploadFile" multiple action="#" :auto-upload="false"
                    :http-request="dealNewFile" :before-upload="beforeUploadF" :before-remove="beforeRemoveF"
                    :file-list="fileList">
                    <el-button size="medium" type="primary">点击上传</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传txt格式文件</div>
                </el-upload>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialogFile = false">取 消</el-button>
                <el-button v-if="uploading" size="small" loading type="primary">上传中</el-button>
                <el-button v-else size="small" type="primary" @click="submitFile">确 定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="提示" :visible.sync="dialogFileList" center width="500px">
            <p class="stutas">共上传 {{fileStatus.all}} 个文件，<span style="color:#3fe562">{{fileStatus.ok}}</span> 个成功，<span style="color:red">{{fileStatus.err}}</span> 个失败</p>
            <el-scrollbar style="max-height: 400px;">
                <ul class="file_ul">
                    <li class="file_li" v-for="(item, index) in fileStatus.data" :key="index">
                        {{index+1}}. {{item.errmsg}}
                    </li>
                </ul>
            </el-scrollbar>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" type="primary" @click="dialogFileList = false">确 定</el-button>
            </span>
        </el-dialog>

        <main-footer></main-footer>
    </div>
</template>

<script>
import mainHeader from '@/components/common/header'
import mainFooter from '@/components/common/footer'
import { pageOptions } from '@/utils/options.js'
import { uploadFile } from "@/utils/uploadFile";
import { new_docs_list, new_docs_detail } from '@/api/import.js'

export default {
    data(){
        return{
            tableData: [],
            fileStatus: {
                data: [],
                all: 0,
                err: 0,
                ok: 0
            },
            formdata: new FormData(),
            timeout: null,
            fileList: [],
            uploadFileList: [],
            uploading: false,
            dialogFile: false,
            dialogFileList: false,
            listLoading: false,
            ...pageOptions
        }
    },
    mounted(){
        this.getPage();
    },
    methods:{
        getPage(){
            this.listLoading = true;
            new_docs_list({
                page: this.currentPage,
                per_page: this.pageSize
            }).then(async res => {
                this.tableData = [];
                this.pageCount = res.data.total;
                this.tableData = res.data.docsList;
                await this.getElements(this.tableData);

                this.$forceUpdate();
                this.listLoading = false;
            })
        },
        async getElements(list){
            for(let i=0; i<list.length; i++){
                if(list[i].newdoc_status == '要素提取成功'){ // && list[i].recommend_status == '类案推荐成功'
                    await new_docs_detail(list[i].docId).then(res => {
                        this.tableData[i].relaInfo_reason = res.data.relaInfo_docCont[2].value[0];
                        this.tableData[i].relaInfo_court = res.data.relaInfo_docCont[0].value[0];
                        this.tableData[i].relaInfo_trialDate = res.data.relaInfo_docCont[4].value[0];
                        
                        this.tableData[i].docContent = res.data.docContent;
                        this.tableData[i].element = res.data.element;
                    })
                }
            }
        },
        details(id, status){
            if(status == '类案推荐成功'){
                this.$router.push({
                    path: '/upload/recommend',
                    query: {
                        docId: id
                    }
                });
            }else{
                this.$msg.warning('案例推荐未成功！');
            }   
        },
        handleCurrentChange(count){
            window.scrollTo(0,0);
            this.currentPage = count;
            this.getPage();
        },
        ...uploadFile
    },
    components: {
        mainHeader,
        mainFooter
    }
}
</script>

<style scoped>
    .btn{
        float: left;
        margin-top: 30px;
        margin-left: 48px;
    }
    .al_ul{
        margin-top: 30px;
        border-top: 1px dashed #c7ccdb;
    }
    .alul480bottom{
        margin-left: 10px;
    }
    .el-upload__tip {
        font-size: 14px;
        color: #f3af2c;
        line-height: 1.6;
        margin-top: 15px;
        position: absolute;
        top: 2px;
        left: 115px;
    }
    .stutas{
        text-align: center;
    }
    .file_ul{
        margin-top: 10px;
        margin-bottom: 30px;
    }
    .file_ul li{
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        color: #999;
        padding-left: 20px;
    }
</style>


