import Vue from 'vue'

export default{
	message: function(msg){
    	Vue.prototype.$notify.closeAll();
		Vue.prototype.$notify({
          	title: '消息',
          	message: msg,
          	type: 'info'
        });
	},
	warning: function(msg){
    	Vue.prototype.$notify.closeAll();
		Vue.prototype.$notify({
          	title: '警告',
          	message: msg,
          	type: 'warning'
        });
	},
	success: function(msg){
    	Vue.prototype.$notify.closeAll();
		Vue.prototype.$notify({
          	title: '成功',
          	message: msg,
          	type: 'success'
        });
	},
	error: function(msg){
    	Vue.prototype.$notify.closeAll();
		Vue.prototype.$notify({
          	title: '错误',
          	message: msg,
          	type: 'error'
        });
	}
}


