import request from '@/utils/ajax'

// 模型列表
export function model_list(params) {
  return request({
    url: '/model-docs-list',
    method: 'get',
    params
  })
}
// 模型详情
export function model_detail(id) {
  return request({
    url: `/model/${id}`,
    method: 'get',
  })
}


