import request from '@/utils/ajax'

// 文件上传
export function upload_file(data) {
  return request({
    url: '/',
    method: 'post',
    data
  })
}
// 推荐
export function recommend(data) {
  return request({
    url: '/recommend',
    method: 'post',
    data
  })
}
// 文件上传列表
export function new_docs_list(params) {
  return request({
    url: '/new-docs-list',
    method: 'get',
    params
  })
}
// 文件上传详情
export function new_docs_detail(id) {
  return request({
    url: `/new/${id}`,
    method: 'get',
  })
}



